export default{
	data(){
		return{
			arr:["http://www.hnlxyj.com/wx/image/my/1.jpg","http://www.hnlxyj.com/wx/image/my/2.jpg","http://www.hnlxyj.com/wx/image/my/3.jpg","http://www.hnlxyj.com/wx/image/my/4.jpg","http://www.hnlxyj.com/wx/image/my/5.jpg","http://www.hnlxyj.com/wx/image/my/6.jpg"]
		}
	},
	methods: {
		save(index) {
			uni.showLoading({
				title: '图片保存中...',
			})
			var that=this;
			const context = uni.createCanvasContext('myCanvas')
			context.drawImage(that.arr[index], 40, 40, 160, 160)
			context.draw(false, function() {
				uni.canvasToTempFilePath({
					canvasId: 'myCanvas',
					success: function(res) {
						uni.getImageInfo({
							src: res.tempFilePath,
							success: function(image) {
								uni.saveImageToPhotosAlbum({
									filePath: image.path,
									success: function() {
										uni.showToast({
											title: '图片保存成功',
											icon: 'none',
											duration: 2200
										});
									},
									fail: function() {
										uni.hideLoading()
										uni.showToast({
											title: "保存失败，请稍后重试",
											icon: "none"
										});
									}
								});
							}
						});
					}
				})
			})				
						
		}
	},
	onNavigationBarButtonTap(e){
		uni.navigateTo({
			url:"enter1"
		})
	}
}
